import React, { useState, useEffect, useRef } from 'react';
import { Target, TrendingUp, Award, X } from 'lucide-react';

const HyperOptimizedLandingPage = () => {
  const [showModal, setShowModal] = useState(false);
  const [email, setEmail] = useState('');
  const [formError, setFormError] = useState('');
  const [emailValid, setEmailValid] = useState(false);
  const videoRef = useRef(null);

  const validateEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const isValid = re.test(String(email).toLowerCase());
    setEmailValid(isValid);
    setFormError(isValid ? '' : 'Enter a valid email address');
  };

  const features = [
    {
      icon: <TrendingUp className="w-12 h-12 text-orange-500" />,
      title: "Predictive AI Mastery",
      description:
        "Eliminate guesswork. Our AI predicts outcomes with 95% accuracy, turning uncertainty into your competitive edge.",
      warning: "Potential winnings slip away with each uninformed bet.",
    },
    {
      icon: <Target className="w-12 h-12 text-orange-500" />,
      title: "Quantum Game Simulation",
      description:
        "Run 10,000+ game scenarios in seconds. Uncover hidden strategies traditional analysis misses.",
      warning: "Competitors are gaining insights while you're left behind.",
    },
    {
      icon: <Award className="w-12 h-12 text-orange-500" />,
      title: "Real-Time Adaptive Insights",
      description:
        "Instant updates on injuries, weather, and team dynamics. Always one step ahead of the market.",
      warning: "Every moment you delay is a potential winning opportunity lost.",
    },
  ];

  return (
    <div className="bg-gray-900 text-white">
      {/* Hero Section */}
      <header className="relative min-h-screen flex items-center justify-center text-center">
        <video
          ref={videoRef}
          autoPlay
          loop
          muted
          playsInline
          className="absolute inset-0 w-full h-full object-cover opacity-30"
        >
          <source src="/path/to/video.mp4" type="video/mp4" />
        </video>
        <div className="relative z-10 max-w-4xl p-8 bg-gray-800 rounded-2xl shadow-lg">
          <h1 className="text-5xl font-extrabold mb-6 text-transparent bg-clip-text bg-gradient-to-r from-orange-500 to-red-500">
            Revolutionize Your Betting Intelligence
          </h1>
          <p className="text-xl mb-8 font-medium">
            Exclusive AI Technology Transforming 10,000+ Bettors from Guesswork
            to Guaranteed Strategies
          </p>
          <button
            onClick={() => setShowModal(true)}
            className="bg-gradient-to-r from-orange-500 to-red-500 text-white px-8 py-4 rounded-full text-lg font-bold shadow-lg hover:scale-105 transition-transform"
          >
            Unlock My Betting Advantage Now
          </button>
        </div>
      </header>

      {/* Features Section */}
      <section className="bg-gray-800 py-20 px-8">
        <div className="max-w-6xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-16 text-transparent bg-clip-text bg-gradient-to-r from-orange-500 to-red-500">
            Transform Your Betting Strategy or Risk Being Left Behind
          </h2>
          <div className="grid md:grid-cols-3 gap-8">
            {features.map((feature, index) => (
              <div
                key={index}
                className="bg-gray-900 p-8 rounded-2xl shadow-md hover:shadow-lg transition-shadow"
              >
                {feature.icon}
                <h3 className="text-2xl font-bold mt-6 mb-4 text-orange-500">
                  {feature.title}
                </h3>
                <p className="mb-6 text-gray-300">{feature.description}</p>
                <div className="bg-red-900 bg-opacity-30 p-4 rounded-lg">
                  <p className="text-red-300 font-medium">{feature.warning}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black bg-opacity-75 z-50 flex items-center justify-center">
          <div className="bg-gray-800 p-6 rounded-lg w-96 shadow-lg">
            <button
              onClick={() => setShowModal(false)}
              className="absolute top-4 right-4 text-gray-400 hover:text-white"
            >
              <X />
            </button>
            <h2 className="text-xl font-bold mb-4 text-orange-500">
              Secure Early Access
            </h2>
            <input
              type="email"
              placeholder="Enter your email"
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
                validateEmail(e.target.value);
              }}
              className="w-full p-3 rounded-lg border border-gray-600 focus:outline-none focus:ring-2 focus:ring-orange-500"
            />
            {formError && (
              <p className="text-red-500 mt-2 text-sm">{formError}</p>
            )}
            <button
              disabled={!emailValid}
              className={`w-full mt-4 p-3 rounded-lg font-bold ${
                emailValid
                  ? "bg-orange-500 text-white hover:bg-orange-600"
                  : "bg-gray-600 text-gray-400 cursor-not-allowed"
              }`}
            >
              Join the Revolution
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default HyperOptimizedLandingPage;